*, :before, :after {
  box-sizing: border-box;
}

:root {
  --accent-color: #687c94;
  --border-color: #e1e7ed;
  --shadow-color: #3d4e611a;
}

@font-face {
  font-family: Avenir, sans-serif;
  src: url("Avenir-Book.657c3568.ttf");
  font-weight: 300;
}

@font-face {
  font-family: Avenir, sans-serif;
  src: url("Avenir-Heavy.01d9ab74.ttf");
  font-weight: 900;
}

body {
  font-family: Avenir, sans-serif;
  font-weight: 300;
}

::placeholder {
  font-family: Avenir, sans-serif;
}

.search__label {
  box-shadow: 0 1px 8px 0 var(--shadow-color);
  align-items: center;
  margin-top: 20px;
  display: flex;
  position: relative;
}

.search__form {
  margin-top: 20px;
}

.search__input {
  border: 1px solid var(--border-color);
  color: var(--accent-color);
  border-radius: 4px;
  width: 100%;
}

.search__input::placeholder {
  color: var(--accent-color);
}

.search__label:before {
  content: "";
  background-image: url("Search.ccd3c07b.svg");
  background-repeat: no-repeat;
  background-size: contain;
  position: absolute;
}

.search__label--big:before {
  width: 19px;
  height: 19px;
  top: 25px;
  left: 26px;
}

.search__input--big {
  height: 70px;
  padding-left: 62px;
  font-size: 16px;
}

.search__label:hover {
  box-shadow: 0 3px 8px 0 var(--shadow-color);
}

.search__input:focus {
  background: linear-gradient(#fff 0%, #f6f6f7 100%);
  outline: none;
  font-weight: 900;
}

.search__label--small:before {
  width: 12px;
  height: 12px;
  top: 15px;
  left: 13px;
}

.search__input--small {
  height: 42px;
  padding-left: 33px;
  font-size: 14px;
}
